@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Poppins', sans-serif}body{background-color:#E5E5E5}


.cv-toolbar{width:100%;padding:17px 24px;background-color:#fff;display:flex;align-items:center;border-bottom:2px solid #cccccc}.cv-toolbar .cv-brand{font-size:18px;font-weight:600;color:#109CF1;margin:0}@media screen and (max-width: 732px){.cv-toolbar .cv-brand{margin-left:0}}@media screen and (max-width: 732px){.cv-toolbar{padding:12px}}

